
"use client"

import * as React from "react"; // Import React
import { TrendingUp } from "lucide-react"
import { Pie, PieChart, Cell, ResponsiveContainer, Tooltip } from "recharts"

import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
} from "@/components/ui/chart"
import type { ChartConfig } from "@/components/ui/chart";

const chartConfig = {
  count: {
    label: "Leads",
  },
  Prospect: { label: "Prospect", color: "hsl(var(--chart-1))" },
  "Call Back": { label: "Call Back", color: "hsl(var(--chart-2))" },
  "Not Interested": { label: "Not Interested", color: "hsl(var(--chart-5))" },
  Ringing: { label: "Ringing", color: "hsl(var(--chart-3))" },
  "Call Disconnected": { label: "Disconnected", color: "hsl(var(--chart-4))" },
  "Won by GRC": { label: "Won", color: "hsl(var(--chart-1))" }, // Use similar color as Prospect or a distinct success color
  "No Outcome": { label: "No Outcome", color: "hsl(var(--muted))" },
} satisfies ChartConfig

interface CallOutcomeChartProps {
   data: { outcome: string; count: number }[];
}

export function CallOutcomeChart({ data }: CallOutcomeChartProps) {
   const chartData = data.map(item => ({
    ...item,
    fill: chartConfig[item.outcome as keyof typeof chartConfig]?.color || "hsl(var(--muted))", // Fallback color
   }));

   const totalCount = React.useMemo(() => {
    return data.reduce((acc, curr) => acc + curr.count, 0)
   }, [data]);

  return (
    <div className="h-[250px] w-full">
     <ChartContainer config={chartConfig} className="aspect-square h-full">
      <ResponsiveContainer width="100%" height="100%">
        <PieChart>
          <ChartTooltip
            cursor={false}
            content={<ChartTooltipContent hideLabel />}
          />
          <Pie
            data={chartData}
            dataKey="count"
            nameKey="outcome"
            innerRadius={60}
            strokeWidth={5}
          >
             {chartData.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={entry.fill}
                className="focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2" // Basic accessibility
              />
            ))}
          </Pie>
        </PieChart>
       </ResponsiveContainer>
     </ChartContainer>
    </div>
  )
}
