
"use client";

import * as React from "react";
import { AppLayout } from "@/components/layout/app-layout";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { CallOutcomeChart } from "@/components/analytics/call-outcome-chart";
import { EmployeePerformanceChart } from "@/components/analytics/employee-performance-chart";
import { LeadSourceChart } from "@/components/analytics/lead-source-chart";
import { useToast } from "@/hooks/use-toast";
import type { Lead } from "@/types/lead";

// Mock function to simulate fetching aggregated analytics data
async function fetchAnalyticsData(): Promise<{
  outcomeCounts: { outcome: string; count: number }[];
  employeePerformance: { employee: string; assigned: number; prospects: number; won: number }[];
  sourceCounts: { source: string; count: number }[];
}> {
  console.log("Fetching analytics data...");
  await new Promise(resolve => setTimeout(resolve, 1200)); // Simulate delay

  // Process mock lead data to generate analytics
   const allLeads: Lead[] = [
        { id: 1, name: "Alice Wonderland", phone: "123-456-7890", company: "Curious Inc.", location: "New York", assignedTo: "Bob", callDate: "2024-09-10", callStatus: "Connected", callOutcome: "Prospect", nadDate: "2024-09-20", remark: "Interested in product A.", uploadDate: "2024-09-01", lastUpdated: "2024-09-10T10:30:00Z", callBookedLost: null, nadBookedLost: null, email: "alice@example.com", source: "Website" },
        { id: 2, name: "Bob The Builder", phone: "987-654-3210", company: "Fixit Corp.", location: "London", assignedTo: "Charlie", callDate: "2024-09-11", callStatus: "Not Connected", callOutcome: "Ringing", nadDate: "2024-09-18", remark: "No answer.", uploadDate: "2024-09-02", lastUpdated: "2024-09-11T11:00:00Z", callBookedLost: null, nadBookedLost: null, email: "bob@example.com", source: "Referral" },
        { id: 3, name: "Charlie Chaplin", phone: "555-123-4567", company: "Silent Films Ltd.", location: "Los Angeles", assignedTo: "Alice", callDate: "2024-09-12", callStatus: "Connected", callOutcome: "Not Interested", nadDate: null, remark: "Budget too low.", uploadDate: "2024-09-03", lastUpdated: "2024-09-12T14:15:00Z", callBookedLost: 'Lost', nadBookedLost: null, email: "charlie@example.com", source: "Upload" },
        { id: 4, name: "Diana Prince", phone: "111-222-3333", location: "Themyscira", assignedTo: "Bob", callDate: "2024-09-14", callStatus: "Connected", callOutcome: "Won by GRC", nadDate: null, remark: "Deal closed!", uploadDate: "2024-09-05", lastUpdated: "2024-09-14T09:00:00Z", callBookedLost: 'Booked', nadBookedLost: null, email: "diana@example.com", source: "Website" },
        { id: 5, name: "Ethan Hunt", phone: "007-007-0070", location: "Global", assignedTo: "Unassigned", callDate: null, callStatus: null, callOutcome: null, nadDate: null, remark: "New lead, needs assignment.", uploadDate: "2024-09-15", lastUpdated: "2024-09-15T16:00:00Z", callBookedLost: null, nadBookedLost: null, email: "ethan@example.com", source: "Upload" },
        { id: 7, name: "Grace Hopper", phone: "101-010-1011", company: "Compilers Co.", location: "Washington D.C.", assignedTo: "Alice", callDate: "2024-09-17", callStatus: "Connected", callOutcome: "Prospect", nadDate: "2024-09-25", remark: "Needs follow-up demo.", uploadDate: "2024-09-06", lastUpdated: "2024-09-17T15:00:00Z", callBookedLost: null, nadBookedLost: null, email: "grace@example.com", source: "Referral" },
        { id: 8, name: "Harry Potter", phone: "481-516-2342", company: "Wizarding World Supplies", location: "Hogwarts", assignedTo: "Charlie", callDate: "2024-09-18", callStatus: "Connected", callOutcome: "Won by GRC", nadDate: null, remark: "Signed contract for magic wands.", uploadDate: "2024-09-07", lastUpdated: "2024-09-18T10:00:00Z", callBookedLost: 'Booked', nadBookedLost: null, email: "harry@example.com", source: "Website" },
        { id: 9, name: "Isaac Newton", phone: "999-888-7777", company: "Gravity Co.", location: "Cambridge", assignedTo: "Bob", callDate: "2024-09-20", callStatus: "Connected", callOutcome: "Call Back", nadDate: "2024-09-28", remark: "Needs apple pricing.", uploadDate: "2024-09-19", lastUpdated: "2024-09-20T11:30:00Z", callBookedLost: null, nadBookedLost: null, email: "isaac@example.com", source: "Referral" },
   ];

  // Calculate Outcome Counts
  const outcomeCountsMap = new Map<string, number>();
  allLeads.forEach(lead => {
    const outcome = lead.callOutcome || "No Outcome";
    outcomeCountsMap.set(outcome, (outcomeCountsMap.get(outcome) || 0) + 1);
  });
  const outcomeCounts = Array.from(outcomeCountsMap, ([outcome, count]) => ({ outcome, count }));

  // Calculate Employee Performance
  const employeePerfMap = new Map<string, { assigned: number; prospects: number; won: number }>();
   const employees = [...new Set(allLeads.map(lead => lead.assignedTo).filter(Boolean) as string[]), "Unassigned"];
   employees.forEach(emp => employeePerfMap.set(emp, { assigned: 0, prospects: 0, won: 0 }));

   allLeads.forEach(lead => {
       const empName = lead.assignedTo || "Unassigned";
       const currentPerf = employeePerfMap.get(empName);
       if(currentPerf) {
          currentPerf.assigned += 1;
          if(lead.callOutcome === 'Prospect') currentPerf.prospects += 1;
          if(lead.callOutcome === 'Won by GRC') currentPerf.won += 1;
          employeePerfMap.set(empName, currentPerf);
       }
   });
   const employeePerformance = Array.from(employeePerfMap, ([employee, data]) => ({ employee, ...data }));


  // Calculate Source Counts
  const sourceCountsMap = new Map<string, number>();
  allLeads.forEach(lead => {
    const source = lead.source || "Unknown";
    sourceCountsMap.set(source, (sourceCountsMap.get(source) || 0) + 1);
  });
  const sourceCounts = Array.from(sourceCountsMap, ([source, count]) => ({ source, count }));

  return { outcomeCounts, employeePerformance, sourceCounts };
}

export default function AnalyticsDashboardPage() {
  const [analyticsData, setAnalyticsData] = React.useState<any>(null); // Use a more specific type
  const [isLoading, setIsLoading] = React.useState(true);
  const { toast } = useToast();

  React.useEffect(() => {
    const loadData = async () => {
      setIsLoading(true);
      try {
        const data = await fetchAnalyticsData();
        setAnalyticsData(data);
      } catch (error) {
        console.error("Failed to load analytics data:", error);
        toast({ variant: "destructive", title: "Error", description: "Could not load analytics data." });
      } finally {
        setIsLoading(false);
      }
    };
    loadData();
  }, [toast]);

  return (
    <AppLayout userRole="admin">
      <h1 className="text-2xl font-semibold text-primary mb-6">Analytics Dashboard</h1>

      {isLoading ? (
        <p>Loading analytics...</p> // Replace with Skeleton loaders
      ) : !analyticsData ? (
         <p className="text-muted-foreground">No analytics data available.</p>
      ) : (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          <Card>
            <CardHeader>
              <CardTitle>Call Outcome Distribution</CardTitle>
              <CardDescription>Breakdown of lead outcomes from calls.</CardDescription>
            </CardHeader>
            <CardContent>
               <CallOutcomeChart data={analyticsData.outcomeCounts} />
            </CardContent>
          </Card>

           <Card className="lg:col-span-2">
            <CardHeader>
              <CardTitle>Employee Performance</CardTitle>
              <CardDescription>Lead handling metrics per employee.</CardDescription>
            </CardHeader>
            <CardContent>
               <EmployeePerformanceChart data={analyticsData.employeePerformance} />
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Lead Source Analysis</CardTitle>
              <CardDescription>Count of leads based on their source.</CardDescription>
            </CardHeader>
            <CardContent>
              <LeadSourceChart data={analyticsData.sourceCounts} />
            </CardContent>
          </Card>

        </div>
      )}
    </AppLayout>
  );
}

